$min-lg: '(min-width: 992px)';
$max-lg: '(max-width: 1199.98px)';
$min-xg: '(min-width: 1200px)';

$max-xs: '(max-width: 575.98px)';
$min-sm: '(min-width: 576px)';
$max-sm: '(max-width: 767.98px)';
$min-md: '(min-width: 768px)';
$max-md: '(max-width: 991.98px)';

header {
  background-color: $bg-color;
  color: $font-color;
  padding: 32px 93px 0 93px;
  box-shadow: inset 0 -2px 0 0 $header-shadow-color;
  top: 0;
  z-index: 3;
  // position: sticky;
}

//Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header {
    padding: 20px 5px;
  }
}
// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  header {
    padding: 20px 10px;
  }
}

// Medium devices (tablets, 768px and up)
@media (max-width: 991.98px) {
  header {
    padding: 20px 10px;
  }
}

// Large devices (desktops, 992px and up)
@media (max-width: 1199.98px) {
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
}

header .upper-header .wrapper-item:nth-of-type(3) {
  text-align: right;
}

header .upper-header .wrapper-item:nth-of-type(1) {
  text-align: left;
}

header .upper-header .wrapper-item .item .makeup-artists,
header .upper-header .wrapper-item .item .phone,
header .upper-header .wrapper-item .item .cart,
header .upper-header .wrapper-item .item .account {
  width: $header-icon-width;
  height: $header-icon-height;
  object-fit: contain;
  margin-bottom: 4px;
}
header .upper-header .wrapper-item .item .makeup-artists a,
header .upper-header .wrapper-item .item .phone a,
header .upper-header .wrapper-item .item .cart a,
header .upper-header .wrapper-item .item .account a {
  display: inline-block;
  width: 32px;
  height: 32px;
}
header .upper-header .wrapper-item .item .account {
  margin-right: 12px;
}
// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header .upper-header .wrapper-item .item .account {
    margin-right: 0px;
  }
}

// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  header .upper-header .wrapper-item .item .account {
    margin-right: 0px;
  }
}

header .upper-header .wrapper-item .item .makeup-artists + span {
  width: 87px;
  height: 16px;
  @extend %header_icon;
}

header .upper-header .wrapper-item .item .phone + span {
  width: 43px;
  height: 16px;
  @extend %header_icon;
}

header .upper-header .wrapper-item .logo {
  width: 118px;
  height: 24px;
  object-fit: contain;
  padding-top: 14px;
}
// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header .upper-header .wrapper-item .logo {
    width: 118px;
    height: 33px;
    object-fit: contain;
    padding-top: 5px;
  }
}

header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(1) {
  // background-color: red !important;
  margin-left: 0;
  position: relative;
}

header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(2) {
  // background-color: red !important;
  margin-left: 23px;
  position: relative;
}
header .upper-header .wrapper-item:nth-of-type(3) .item:nth-of-type(1) {
  padding-right: 47px;
  position: relative;
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(2) {
    margin-left: 0px;
    padding-right: 20px;
    padding-left: 20px;
  }
  header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(1) {
    padding-right: 0;
  }
  header .upper-header .wrapper-item:nth-of-type(3) .item:nth-of-type(1) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(2) {
    margin-left: 0px;
    padding-right: 20px;
    padding-left: 20px;
  }
  header .upper-header .wrapper-item:nth-of-type(1) .item:nth-of-type(1) {
    padding-right: 0;
  }
  header .upper-header .wrapper-item:nth-of-type(3) .item:nth-of-type(1) {
    padding-right: 20px;
    padding-left: 20px;
  }
}

header .upper-header .wrapper-item .item .cart .assets {
  position: absolute;
  top: -20px;
  left: 6px;
  width: 20px;
  height: 20px;
  // background-color: green !important;
}

//
// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  header .upper-header .wrapper-item .item .cart .assets {
    top: -17px;
    left: 26px;
  }
}

// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  header .upper-header .wrapper-item .item .cart .assets {
    top: -16px;
    left: 26px;
  }
}

header .upper-header .wrapper-item .item .cart span.cart-quantity {
  position: absolute;
  top: 2px;
  left: 5px;
  z-index: 2;
  width: 8px;
  height: 20px;
  @extend %cart_quantity;
}
header .upper-header .wrapper-item .item .cart + span {
  width: 26px;
  height: 16px;
  @extend %header_icon;
}

header .upper-header .wrapper-item .item .account + span {
  width: 58px;
  height: 16px;
  @extend %header_icon;
}

header .upper-header .wrapper-item .item > span:hover {
  color: #edb15d;
  cursor: pointer;
}
// navbar //1224

.navbar {
  padding: 40px 0 28px 0px;
}
//Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  .navbar {
    padding: 10px 0;
  }
}

// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  .navbar {
    padding: 13px 0;
  }
}

// Medium devices (tablets, 768px and up)
@media (max-width: 991.98px) {
  .navbar {
    padding: 15px 0;
  }
}

li.nav-item {
  padding-right: 32px;
}
// Large devices (desktops, 992px and up)
@media (max-width: 1199.98px) {
  li.nav-item {
    padding-right: 0;
  }
}

a.nav-link {
  padding: 0;
}
.navbar-dark .navbar-nav .nav-link {
  color: $font-color;
  &:hover {
    color: #edb15d;
  }
}

.menu-group nav ul li.products a {
  display: inline-block;
  width: 84px;
  height: 16px;
  @extend %menue_item;
}
.menu-group nav ul li.online-catalogues a {
  display: inline-block;
  width: 164px;
  height: 16px;
  @extend %menue_item;
}
.menu-group nav ul li.find-our-stores a {
  display: inline-block;
  width: 139px;
  height: 16px;
  @extend %menue_item;
}
.menu-group nav ul li.new-products a {
  display: inline-block;
  width: 124px;
  height: 16px;
  @extend %menue_item;
}
.menu-group nav ul li.about-us a {
  display: inline-block;
  width: 80px;
  height: 16px;
  @extend %menue_item;
}
.menu-group nav ul li.contact-us a {
  display: inline-block;
  width: 98px;
  height: 16px;
  @extend %menue_item;
}
//search
form {
  &::after {
    content: '';
    width: 292px;
    height: 1px;
    border: 1px solid $font-color;
  }
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
  form {
    margin-top: 6px;
    &::after {
      width: 100%;
      height: 1px;
    }
  }
}

// Small devices (landscape phones, 576px and up)
@media (max-width: 767.98px) {
  form {
    margin-top: 6px;
    &::after {
      width: 649px;
    }
  }
}

// Medium devices (tablets, 768px and up)
@media (max-width: 991.98px) {
  form {
    &::after {
      width: 751px;
    }
  }
}

form input[type='text'] {
  width: 192px;
  height: 16px;
  font-stretch: $font-stretch;
  font-style: $font-style;
  line-height: $input-lineheight;
  background-color: $bg-color;
  color: $font-color;
  border: none;
  outline: none;
  &:focus {
    background-color: $bg-color;
    box-shadow: none;
    outline: none;
  }
}
form .input-group input::placeholder {
  width: 192px;
  height: 16px;
  opacity: 0.4;
  font-family: $font-family;
  font-size: $font-size;
  font-weight: $font-weight;
  font-stretch: $font-stretch;
  font-style: $font-style;
  line-height: $input-lineheight;
  letter-spacing: $letter-spacing;
  color: $font-color;
}
form .input-group .input-group-text {
  background-color: $bg-color;
  padding: 0;
  border: none;
}

.custom-toggler.navbar-toggler:hover {
  border-color: #edb15d;
}
.custom-toggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(250, 250, 250, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

header button:focus {
  outline: none;
}
