.cate {
  padding-top: 60px;
  padding-bottom: 32px;
  background-color: #fff;
  height: 565px;
}
.cate h4 {
  width: 184px;
  height: 32px;
  font-family: Eina03;
  font-size: 24px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: 5px;
  text-align: center;
  color: #000;
}
.cate .header {
  &::after {
    content: '';
    width: 45px;
    height: 1px;
    border: 1px solid #000;
  }
  &::before {
    content: '';
    width: 45px;
    height: 1px;
    border: 1px solid #000;
  }
}

.cate h3 {
  width: 240px;
  height: 60px;
  font-family: Cochin;
  font-size: 56px;
  font-weight: bold;
  font-stretch: normal;
  font-style: italic;
  line-height: 1.07;
  letter-spacing: normal;
  text-align: center;
  color: #000;
}
.cate button {
  width: 254px;
  height: 64px;
  background-color: #edb15d;
  border: none;
}

.cate button a {
  width: 133px;
  height: 16px;
  font-family: Eina03;
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 1px;
  text-align: center;
  color: #000;
}

.cate .wrapper-item {
  padding: 60px 250px 100px 250px;
}
.cate .wrapper-item .item span {
  font-family: Eina03;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 1px;
  text-align: center;
  color: #000;
  padding-top: 19px;
}
