section {
  height: 526px;
}
@media (max-width: 575.98px) {
  section {
    height: 350px;
  }
}

section .bannar {
  background-image: url('../../imag-pro/image/hero-img.png');
  height: 526px;
  background-size: cover;
}
@media (max-width: 575.98px) {
  section .bannar {
    height: 350px;
  }
}

section .bannar img {
  padding-top: 82px;
  padding-left: 260px;
}

@media (max-width: 575.98px) {
  section .bannar img {
    padding-top: 0;
    padding-left: 0;
    max-width: 100%;
  }
}
.bannar .btn1 button {
  width: 270px;
  height: 64px;
  border: solid 1px #edb15d;
  background-color: transparent;
  margin-top: 40px;
  margin-left: 310px;
  // padding: 35px 23px;
  &:hover {
    background-color: #edb15d;
    color: #000;
  }
  &:focus {
    outline: none;
  }
}
@media (max-width: 575.98px) {
  .bannar .btn1 button {
    margin-left: 23px;
    margin-top: 30px;
  }
}

.bannar .btn1 button:hover a {
  color: #000;
}

.bannar .btn1 button a {
  font-family: Eina03;
  font-size: 14px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 1.5px;
  text-align: center;
  color: #edb15d;
  padding: 35px 8px;
  &:link {
    text-decoration: none;
  }
}
.bannar .btn {
  margin-top: 40px;
}
.bannar .head h3.he1 {
  width: 679px;
  height: 56px;
  font-family: Eina03;
  font-size: 17px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.65;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  padding-top: 40px;
  padding-left: 197px;
}
@media (max-width: 575.98px) {
  .bannar .head h3.he1 {
    width: 359px;
    height: 34px;
    padding-top: 23px;
    padding-left: 0;
    font-size: 16px;
  }
}
.bannar .head p.he2 {
  width: 679px;
  font-family: Eina03;
  font-size: 17px;
  font-weight: bold !important;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.65;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  font-weight: bold;
  padding: 0;
  margin: 0;
  height: 25px;
  padding-left: 154px;
}
@media (max-width: 575.98px) {
  .bannar .head p.he2 {
    width: 359px;
    font-size: 12px;
  }
}
@media (max-width: 767.98px) {
  .bannar .head p.he2 {
    width: 100%;
  }
}
