.slider {
  height: 526px;
}

.slider {
  background-image: url('../../imag-pro/image/best-sellers-bg.png');
  //   background-image: url('../i/1.png');
  height: 944px;
  background-size: cover;
}

.slider .slide-head {
  padding-top: 115px;
  padding-left: 108px;
}

@media (max-width: 575.98px) {
  .slider .slide-head {
    padding-top: 50px;
    padding-left: 20px;
  }
}
@media (max-width: 767.98px) {
  .slider .slide-head {
    padding-top: 55px;
    padding-left: 25px;
  }
}

.slider .slide-link {
  padding-top: 192px;
}
@media (max-width: 575.98px) {
  .slider .slide-link {
    padding-top: 50px;
  }
}
@media (max-width: 767.98px) {
  .slider .slide-link {
    padding-top: 55px;
  }
}
.slider .slide-link a {
  padding-right: 108px;
  // width: 74px;
  height: 16px;
  font-family: Eina03;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 1px;
  text-align: right;
  color: #ffffff;
}

@media (max-width: 575.98px) {
  .slider .slide-link a {
    padding-right: 20px;
  }
}
@media (max-width: 767.98px) {
  .slider .slide-link a {
    padding-right: 25px;
  }
}
.slider .slide-head h2 {
  // width: 292px;
  // height: 60px;
  font-family: Cochin;
  font-size: 56px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.07;
  letter-spacing: normal;
  color: #ffffff;
}
@media (max-width: 575.98px) {
  .slider .slide-head h2 {
    font-size: 24px;
  }
}
@media (max-width: 767.98px) {
  .slider .slide-head h2 {
    font-size: 25px;
  }
}
.slider .slide-head h4 {
  // width: 496px;
  // height: 28px;
  font-family: Eina03;
  font-size: 17px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.65;
  letter-spacing: normal;
  color: #ffffff;
}
@media (max-width: 575.98px) {
  .slider .slide-head h4 {
    font-size: 10px;
  }
}
@media (max-width: 767.98px) {
  .slider .slide-head h4 {
    font-size: 11px;
  }
}
.slider .wrapper-item-slide {
  padding-top: 107px;
  padding-left: 108px;
}

@media (max-width: 575.98px) {
  .slider .wrapper-item-slide {
    padding-top: 50px;
    padding-left: 20px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide {
    padding-top: 55px;
    padding-left: 25px;
  }
}

.slider .wrapper-item-slide .item {
  width: 288px !important;
  height: 468px;
  box-shadow: 0 40px 48px 0 rgba(30, 30, 31, 0.16);
  background-color: #ffffff;
  margin-right: 24px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item {
    width: 100px !important;
    height: 300px;
    margin-right: 10px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item {
    width: 120px !important;
    height: 350px;
    margin-right: 15px;
  }
}

.slider .wrapper-item-slide .item:focus {
  outline: none;
}
.slider .wrapper-item-slide .item .product {
  width: 288px;
  height: 468px;
  object-fit: contain;
  mix-blend-mode: darken;
  background-color: gray;
  position: relative;
}

@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product {
    width: 100px !important;
    height: 300px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product {
    width: 120px !important;
    height: 350px;
  }
}

.slider .wrapper-item-slide .item .product .card {
  width: 288px;
  height: 468px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product .card {
    width: 100px !important;
    height: 300px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product .card {
    width: 120px !important;
    height: 350px;
  }
}

.slider .wrapper-item-slide .item .product img {
  width: 252px;
  margin: auto;
}

@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product img {
    width: 100px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product img {
    width: 110px;
  }
}
.slider .wrapper-item-slide .item .product .card-body {
  width: 256px;
  padding: 16px 12px 32px 12px;
  margin: auto;
}

@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product .card-body {
    width: 104px;
    padding: 10px 5px 20px 5px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product .card-body {
    width: 104px;
    padding: 12px 8px 24px 8px;
  }
}
.slider .wrapper-item-slide .item .product h5 {
  font-family: Eina03;
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: 0.3px;
  text-align: center;
  color: #000;
  width: 256px;
  height: 24px;
  margin: auto;
  // padding-bottom: 16px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product h5 {
    font-size: 14px;
    width: 104px;
    height: 14x;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product h5 {
    font-size: 16px;
    width: 108px;
    height: 18x;
  }
}

.slider .wrapper-item-slide .item .product .separator {
  width: 240px;
  height: 1px;
  opacity: 0.1;
  background-color: red;
  border: solid 1px #000;
  margin-top: 40px;
  margin-bottom: 16px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product .separator {
    margin-top: 20px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product .separator {
    margin-top: 22px;
  }
}
.slider .wrapper-item-slide .item .product p {
  width: 178px;
  margin: auto;
  font-family: Eina03;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: 0.4px;
  text-align: center;
  color: #000;
  padding-bottom: 35px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product p {
    width: 100px;
    font-size: 10px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product p {
    width: 100px;
    font-size: 10px;
    padding-bottom: 30px;
  }
}

.slider .wrapper-item-slide .item .product h6 {
  font-family: Eina03;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.14;
  letter-spacing: 0.5px;
  text-align: center;
  color: #000;
  padding-bottom: 32px;
}
@media (max-width: 575.98px) {
  .slider .wrapper-item-slide .item .product h6 {
    font-size: 10px;
    padding-bottom: 32px;
  }
}
@media (max-width: 767.98px) {
  .slider .wrapper-item-slide .item .product h6 {
    font-size: 11px;
    padding-bottom: 38px;
  }
}
